@font-face {
  font-family: "AleckCdBlack";
  src: local("AleckCdBlack"),
   url("./fonts/ATTAleckCd_Blk.ttf") format("truetype");
  font-weight: normal;
  }

  @font-face {
    font-family: "AleckCdMed";
    src: local("AleckCdMed"),
     url("./fonts/ATTAleckCd_Md.ttf") format("truetype");
    font-weight: normal;
    }

@font-face {
  font-family: "AleckSans";
  src: local("AleckSans"),
    url("./fonts/ATTAleckSans_Rg.ttf") format("truetype");
  font-weight: normal;
  }

  body {
    padding-bottom: env(safe-area-inset-bottom);
    height: 90vh;
    overflow-y: hidden;
      -youbkit-touch-callout: none; /* iOS Safari */
      -youbkit-user-select: none;   /* Chrome 6.0+, Safari 3.1+, Edge & Opera 15+ */
      -moz-user-select: none;    /* Firefox */
      -ms-user-select: none;        /* IE 10+ and Edge */
      user-select: none;
      overflow: hidden;
      position: fixed;
      width: 100%;
}

  * {
    box-sizing: border-box;  
  }

.App {
  text-align: center;
  width: 100%;
  height: 100vh;
  overflow-y: hidden;
}

a{
  color: white;
}

#buttonUp{
  background-image: url("arrow_up.png");
  background-repeat: no-repeat;
  background-size: 33%;
  background-position: center;
}

#buttonDown{
  background-image: url("arrow_down.png");
  background-repeat: no-repeat;
  background-size: 33%;
  background-position: center;
}

#buttonLeft{
  background-image: url("arrow_left.png");
  background-repeat: no-repeat;
  background-size: 33%;
  background-position: center;
}

#buttonRight{
  background-image: url("arrow_right.png");
  background-repeat: no-repeat;
  background-size: 33%;
  background-position: center;
}

.buttonMain:active {
  background-color: #bfcacf;
  transform: translateY(12.5%);
}

.buttonSquare:active {
  background-color: #bfcacf;
  transform: translateY(12.5%);
}

.barcodeSection{
  z-index: 5;
  position: absolute;
  top: 2.5%;
  /* background-color: red; */
  height: 20%;
  width: 100%;
}
.barcodeContainer{
  z-index: 5;
  position: relative;
  background-color: white;
  height: 75%;
  top: 12.5%;
  aspect-ratio: 3 / 1;
  margin: 0 auto;
  border: .25em solid #0057B8;
}

